import { useState, useEffect } from "react";
import { Link as RRDLink } from "react-router-dom";
import server from "../apis/server";

function Link({ to, children, isUser, userId }) {
  const [userName, setUserName] = useState(null);

  useEffect(() => {
    if (isUser) {
      server.get(`/users/${userId}`).then((res) => setUserName(res.data.name));
    }
  }, [userId]);

  if (!isUser) {
    return (
      <RRDLink to={to} style={{ color: "black" }}>
        {children}
      </RRDLink>
    );
  } else if (userName !== null) {
    return (
      <RRDLink to={to} style={{ color: "black" }}>
        {userName}
      </RRDLink>
    );
  } else {
    return null;
  }
}

export default Link;
